import styled from "styled-components";
import { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate, useLocation } from "react-router-dom";
import HoverModal from "components/modal/HoverModal";
import LoadingAnimation from "components/modal/LoadingAnimation";
import AskModal from "components/modal/AskModal";

function Mypage() {
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [email, setEmail] = useState("");
  const [userInfo, setUserInfo] = useState("");
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(true);
  const [modalOpen, setModalOpen] = useState(false);
  const [notiModal, setNotiModal] = useState(false);
  const [currModalContent, setCurrModalContent] = useState("");
  const [askModal, setAskModal] = useState(false);

  const handleNotiModal = () => {
    setNotiModal((e) => !e);
    if (notiModal) {
      navigate("/");
    }
  };

  const handleAskModal = () => {
    setAskModal((e) => !e);
  };

  const handleUserInfo = () => {
    axios({
      url: "/api/v1/users",
      method: "GET",
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
    })
      .then((res) => {
        setIsLoading(false);
        setUserInfo(res.data.responseBody);
      })
      .catch((error) => {
        // navigate("/authentication/sign-in");
      });
  };

  useEffect(() => {
    // @ts-ignore
    handleUserInfo();
  }, []);

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handlePasswordConfirm = (e) => {
    setPasswordConfirm(e.target.value);
  };

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handleClose = () => {
    navigate("/");
  };

  const handleUpdateInfo = (event) => {
    if (password === "") {
      alert(" ");
      return;
    }

    if (password !== passwordConfirm) {
      alert("  .");
      return;
    }

    axios({
      url: "/api/v1/users",
      method: "PUT",
      withCredentials: true,
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
      data: {
        userEmail: email,
        password: password,
      },
    })
      .then((res) => {})
      .catch((error) => {
        setCurrModalContent(" .");
        // navigate("/authentication/sign-in");
      });
    setCurrModalContent(" .");
    setNotiModal(true);
  };

  async function handleDelete() {
    handleAskModal();
    axios({
      url: "/api/v1/users",
      method: "DELETE",
      withCredentials: true,
      headers: {
        Authorization: "Bearer " + localStorage.getItem("token"),
      },
    })
      .then((res) => {
        setCurrModalContent(" .");
        localStorage.removeItem("token");
        // navigate("/");
        // setNotiModal(true);
      })
      .catch((error) => {
        setCurrModalContent(" .");
        // navigate("/authentication/sign-in");
      });

    setNotiModal(true);
  }

  const handlAskModal = () => {
    setCurrModalContent("  ?");
    setAskModal(true);
  };

  return (
    <BodyContainer>
      <BodyWrapper>
        {isLoading ? (
          <LoadingAnimation />
        ) : (
          <MemberContainer>
            <LeftContainer>
              <UserIcon>
                <i className="bi bi-person-circle"> </i>
              </UserIcon>
              <InputFile>
                <label htmlFor="formFile" className="form-label"></label>
                <input className="form-control" type="file" id="formFile" />
              </InputFile>
            </LeftContainer>

            <RightContainer>
              <Form>
                <label></label>
                <ID>{userInfo.userName}</ID>
                <label></label>
                <Input placeholder={userInfo.userEmail} type="email" name="password" onChange={handleEmailChange} />
                <label></label>
                <Input placeholder={""} type="password" name="password" onChange={handlePasswordChange} />
                <label> </label>
                <Input placeholder={" "} type="password" name="password" onChange={handlePasswordConfirm} />
                <div className="d-grid gap-2 d-md-block mt-4 d-md-flex justify-content-md-center">
                  <button type="button" className="btn btn-outline-danger px-5 me-md-2" onClick={handlAskModal}>
                    
                  </button>
                  <button type="button" className="btn btn-outline-danger px-5 me-md-2" onClick={handleClose}>
                    
                  </button>
                  <button type="button" className="btn btn-primary px-5" onClick={handleUpdateInfo}>
                    
                  </button>
                </div>
              </Form>

              {notiModal && <HoverModal handleModal={handleNotiModal} currModalContent={currModalContent} />}
              {askModal && <AskModal handleModal={handleAskModal} currModalContent={currModalContent} activeAxios={handleDelete} />}
            </RightContainer>
          </MemberContainer>
        )}
      </BodyWrapper>
    </BodyContainer>
  );
}

export default Mypage;

const BodyContainer = styled.div`
  width: 100%;
  height: 100%;
`;

const BodyWrapper = styled.div`
  width: 850px;
  margin: auto;
  padding: 30px 0;
`;
const MemberContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  border: 1px solid rgba(245, 245, 245, 0.7);
  border-radius: 8px;
  padding: 40px;
`;

const LeftContainer = styled.div`
  font-size: 100px;

  width: 40%;
`;
const RightContainer = styled.div`
  width: 60%;
  padding: 10px;
`;

const UserIcon = styled.div`
  text-align: center;
  color: #807979;
`;

const Input = styled.input`
  max-width: 100%;
  padding: 11px 13px;
  background: #f9f9fa;
  color: black;
  margin-bottom: 0.9rem;
  border-radius: 4px;
  outline: 0;
  border: 1px solid rgba(245, 245, 245, 0.7);
  font-size: 14px;
  transition: all 0.3s ease-out;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1), 0 1px 1px rgba(0, 0, 0, 0.1);

  :focus,
  :hover {
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.15), 0 1px 5px rgba(0, 0, 0, 0.1);
  }
`;

const Form = styled.div`
  margin: 0 auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
`;

const InputFile = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 5px;
`;

const ID = styled.div`
  padding: 7px 12px;
`;
